<template>
  <div id="app">
    <Screen />
    <ControlBar />
  </div>
</template>

<script>
import Screen from "./components/Screen.vue";
import ControlBar from "./components/ControlBar.vue";
import { eventBus } from "@/main.js";

export default {
  name: "app",
  components: {
    Screen,
    ControlBar,
  },
  data() {
    return {
      docBootstrapSize: "",
      timer: 0,
      displayMode: "night"
    };
  },
  watch: {
    docBootstrapSize(val, oldVal) {
      if(val !== oldVal) eventBus.$emit("windowSizeChanged", val);
    }
  },
  methods: {
    onToggleDayNight() {
      if(this.displayMode === "night") {
        window.document.querySelector("#app").style.setProperty("--primary-color", "teal");
        window.document.querySelector("#app").style.setProperty("--accent-color", "green");
        window.document.querySelector("#app").style.setProperty("--primary-text-color", "black");
        window.document.querySelector("#app").style.setProperty("--secondary-text-color", "green");
        window.document.querySelector("#app").style.setProperty("--background-color", "whitesmoke");
        this.displayMode = "day";
        eventBus.$emit("displayModeChanged", this.displayMode);
        this.$forceUpdate();
      }
      else {
        window.document.querySelector("#app").style.setProperty("--primary-color", "teal");
        window.document.querySelector("#app").style.setProperty("--accent-color", "green");
        window.document.querySelector("#app").style.setProperty("--primary-text-color", "green");
        window.document.querySelector("#app").style.setProperty("--secondary-text-color", "white");
        window.document.querySelector("#app").style.setProperty("--background-color", "rgba(0, 0, 0, 0.9)");
        this.displayMode = "night";
        eventBus.$emit("displayModeChanged", this.displayMode);
        this.$forceUpdate();
      }
    }
  },
  created() {
    eventBus.$on("toggleDayNight", this.onToggleDayNight);
  },
  mounted() {
    this.timer = setInterval(() => {
      const docWidth = window.document.querySelector("body").clientWidth;
      if(docWidth < 576) {
        this.docBootstrapSize = "xs";
      }
      else if(docWidth >= 1200 ) {
        this.docBootstrapSize = "xl";
      }
      else if(docWidth >= 992 ) {
        this.docBootstrapSize = "lg";
      }
      else if(docWidth >= 768 ) {
        this.docBootstrapSize = "md";
      }
      else if(docWidth >= 576 ) {
        this.docBootstrapSize = "sm";
      }
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

