<template>
  <div class="container">
    <div id="console" style="width: 100%;">
      <input
        type="text"
        id="console-inp"
        ref="console-inp"
        :style="commandInputStyle"
        placeholder="~$ command here"
        v-model="command"
        @keypress.enter="onRunCommand"
      />
      <button id="console-exec-btn" @click="onRunCommand">&gt;</button>
    </div>
    <ul v-if="!isMobile" id="right-items">
      <li id="projects-view-btn" title="projects" @click="onProjectsClick">
        <img src="@/assets/projects.svg" class="control-bar-icons" alt="projects" />
      </li>
      <li id="contact-view-btn" title="contact" @click="onContactClick">
        <img src="@/assets/contact.svg" class="control-bar-icons" alt="contact" />
      </li>
      <li id="about-view-btn" title="about" @click="onAboutClick">
        <img src="@/assets/about.svg" class="control-bar-icons" alt="about" />
      </li>
    </ul>
  </div>
</template>

<script>
import { eventBus } from "@/main.js";

export default {
  data: () => ({
    command: "",
    isMobile: true
  }),
  computed: {
    commandInputStyle() {
      return {
        width: this.isMobile ? "100% !important" : ""
      };
    }
  },
  methods: {
    showOverlay() {
      eventBus.$emit("showOverlay");
    },
    hideOverlay() {
      eventBus.$emit("hideOverlay");
    },
    onWindowSizeChanged(newSize) {
      if (newSize === "xs" || newSize === "sm") this.isMobile = true;
      else this.isMobile = false;
    },
    onRunCommand() {
      if (this.command.length > 0) {
        this.pushToHistory(this.command);
        switch (this.command.toLowerCase()) {
          case "aceix -p":
          case "aceix --projects":
            this.onProjectsClick();
            break;
          case "aceix -a":
          case "aceix --about":
            this.onAboutClick();
            break;
          case "aceix -c":
          case "aceix --contact":
            this.onContactClick();
            break;
          case "cls":
          case "clear":
            this.pushToHistory("clear");
            break;
          case "exit":
            this.hideOverlay();
            break;
          default:
          this.onShowHelp();
        }
        this.command = "";
      }
    },
    onShowHelp() {
      let help = `<pre>
      USE aceix --command\n

      COMMAND    SHORT   DESCRIPTION

      help               DISPLAY THIS HELP
      projects   -p      OUTLINE PROJECTS DONE BY @the_aceix
      about      -a      SHOW @the_aceix's PROFILE
      contact    -c      REACH @the_aceix

      Example: aceix -p
      type "exit" to go back
      </pre>`;

      this.pushToHistory(help);
    },
    pushToHistory(str) {
      str = String(str);
      if (str === "clear") {
        this.$root.$children[0].$children[0].history.length = 0;
      }
      this.$root.$children[0].$children[0].history.push(str);
    },
    onProjectsClick() {
      this.$root.$children[0].$children[0].currentComponent = "ProjectsSlide";
      if(this.isMobile) this.showOverlay();
    },
    onContactClick() {
      this.$root.$children[0].$children[0].currentComponent = "ContactSlide";
      if(this.isMobile) this.showOverlay();
    },
    onAboutClick() {
      this.$root.$children[0].$children[0].currentComponent = "AboutSlide";
      if(this.isMobile) this.showOverlay();
    }
  },
  created() {
    eventBus.$on("windowSizeChanged", this.onWindowSizeChanged);
  },
  mounted() {
    this.$refs["console-inp"].focus();
  }
};
</script>

