<template>
  <div class="projects-slide">
    <main class="card">
      <h2 id="heading">Some projects</h2>
      <em v-if="!projects.length">
        No project to display
        <br />
      </em>
      <template v-else>
        <section class="project" v-for="(p, i) in projects" :key="`proj-${i}`" :style="projectStyle">
          <img :src="p.imgSrc" alt="project-img" class="project-img" :style="projectImgStyle" />
          <div class="project-details" :style="projectDetailsStyle">
            <h4 class="project-title">{{ p.title }}</h4>
            <p class="project-desc">{{ p.desc }}</p>
            <div class="two-col-row">
              <a v-if="p.projURL" :href="p.projURL" target="_blank" class="btn-link">Site</a>
              <a v-if="p.github" :href="p.github" target="_blank" class="btn-link">Github</a>
            </div>
          </div>
        </section>
      </template>
      <footer id="foot">
        <div class="">
          See my
          <a href="https://github.com/Aceix" target="_blank">Github</a>
        </div>
        <div class="">
          Query me on
          <a href="https://twitter.com/the_aceix" target="_blank">Twitter</a>
        </div>
      </footer>
    </main>
  </div>
</template>

<script>
import { eventBus } from "@/main.js";
let db = window.firebase.firestore();
db.settings({ timestampsInSnapshots: true });

export default {
  data: () => ({
    projects: [],
    isMobile: true
  }),
  computed: {
    projectStyle() {
      return {
        "flex-flow": this.isMobile ? "column nowrap" : ""
      };
    },
    projectImgStyle() {
      return {
        "height": this.isMobile ? "100px !important" : ""
      };
    },
    projectDetailsStyle() {
      return {
        "margin-top": this.isMobile ? "1rem !important" : "",
        "margin-bottom": this.isMobile ? "1rem !important" : ""
      };
    }
  },
  methods: {
    onWindowSizeChanged(newSize) {
      if (newSize === "xs" || newSize === "sm") this.isMobile = true;
      else this.isMobile = false;
    }
  },
  created() {
    // load projects
    db.collection("projects")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let d = doc.data();
          this.projects.push({
            imgSrc: d.img,
            projURL: d.site,
            github: d.github,
            title: d.title,
            desc: d.desc,
          });
        });
      });
    eventBus.$on("windowSizeChanged", this.onWindowSizeChanged);
  },
};
</script>

