<template>
  <div class="container">
    <section v-show="!(isMobile && isSlideVisible)" id="history">
      <button id="day-night-toggle-btn" @click="toggleDayNight" title="Toggle day/night mode">
        <img v-if="displayMode === 'day'" style="width: 20px; height: 20px;" src="@/assets/night.svg" alt="TOGGLE">
        <img v-else src="@/assets/day.svg" style="width: 20px; height: 20px;" alt="TOGGLE">
      </button>
      <ul id="history-items">
        <li class="history-item" v-for="(command, i) in history" :key="i">
          <span v-if="i !== 1" class="highlighted">visitor@the_aceix:~$</span>&nbsp;
          <span v-html="command" />
        </li>
      </ul>
      <p id="dummy-prompt">
        <span class>visitor@the_aceix:~$</span>&nbsp;
        <span class="cursor"></span>
      </p>
    </section>
    <section v-if="!isMobile" id="right-pane">
      <component :is="currentComponent"></component>
    </section>
    <template v-else>
      <section v-show="isSlideVisible" id="overlay-pane">
        <component :is="currentComponent"></component>
      </section>
    </template>
  </div>
</template>

<script>
import AboutSlide from "./AboutSlide.vue";
import ProjectsSlide from "./ProjectsSlide.vue";
import ContactSlide from "./ContactSlide.vue";
import { eventBus } from "@/main.js";

export default {
  components: {
    AboutSlide,
    ProjectsSlide,
    ContactSlide,
  },
  data() {
    return {
      history: [
        "whatis aceix",
        `<pre>
         aceix --help
         aceix --projects
         aceix --about
         aceix --contact
         
         type "exit" to go back</pre>`,
      ],
      currentComponent: "AboutSlide",
      isMobile: true,
      isSlideVisible: false,
      displayMode: "night"
    };
  },
  watch: {
    history() {
      this.$nextTick(() => {
        window.document.querySelector("#history").scrollTo({
          top: window.document.querySelector("#history").clientHeight + 1000,
          behavior: "smooth"
        })
      });
    }
  },
  methods: {
    toggleDayNight() {
      eventBus.$emit("toggleDayNight");
    },
    onShowOverlay() {
      this.isSlideVisible = true;
    },
    onHideOverlay() {
      this.isSlideVisible = false;
    },
    onWindowSizeChanged(newSize) {
      if (newSize === "xs" || newSize === "sm") this.isMobile = true;
      else this.isMobile = false;
    }
  },
  created() {
    eventBus.$on("windowSizeChanged", this.onWindowSizeChanged);
    eventBus.$on("hideOverlay", this.onHideOverlay);
    eventBus.$on("showOverlay", this.onShowOverlay);
    eventBus.$on("displayModeChanged", (mode) => {
      this.displayMode = mode;
    });
  },
};
</script>

<style scoped>
.container {
  display: flex;
  flex-flow: row nowrap;
  padding: 10px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
#history {
  position: relative;
  margin-right: 10px;
  border: 1px dashed var(--accent-color);
  border-radius: 7px;
  height: 100%;
  padding: 4px 10px;
  overflow: auto;
  flex: 3 0 0;
}
#right-pane {
  flex: 1 0 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0.7;
}
#overlay-pane {
  margin-right: 10px;
  height: 100%;
  padding: 4px 10px;
  overflow: auto;
  z-index: 2;
  position: absolute;
}
.cursor {
  display: inline-block;
  width: 7px;
  height: 15px;
  margin-bottom: -2px;
  border: 1px solid var(--primary-text-color);
  background-color: var(--background-color);
  animation: soft-blink 1.5s ease-out infinite;
}
#history-items {
  list-style: none;
  padding: 0pc;
}
.highlighted {
  color: rgba(137, 43, 226, 0.6);
}
#day-night-toggle-btn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: transparent;
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  line-height: 100%;
}

@keyframes soft-blink {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
</style>
