<template>
  <div class="contact-slide">
    <main class="card">
      <section id="welcome-section">
        <b>Hello!</b>
        <br />Wanna say hi?
        <br />follow the &lt;protocol/&gt;
      </section>
      <div class="divider"></div>
      <section id="form-section">
        <div id="contact-form">
          <input type="text" name="form-name" id="form-name" placeholder="const string Name" v-model.trim="form.name" />
          <small v-if="errForm.name" class="form-err">{{ errForm.name }}</small>
          <br />
          <input type="text" name="form-contact" id="form-contact" placeholder="Email || Mobile" v-model.trim="form.contact" />
          <small v-if="errForm.contact" class="form-err">{{ errForm.contact }}</small>
          <br />
          <textarea
            name="form-msg"
            id="form-msg"
            cols="30"
            rows="10"
            placeholder="Write something..."
            v-model.trim="form.message"
          ></textarea>
          <small v-if="errForm.message" class="form-err">{{ errForm.message }}</small>
          <br />
          <input type="button" id="form-submit-btn" value="PING" @click="sendMessage" />
          <br />
        </div>
      </section>
    </main>
  </div>
</template>

<script>
export default {
  name: "contact-slide",
  data() {
    return {
      form: {
        name: "",
        contact: "",
        message: "",
      },
      errForm: {},
    };
  },
  methods: {
    isFormValid() {
      this.errForm = {};
      if(!this.form.name) {
        this.errForm.name = "I would love to know your name"
        return false;
      }
      if(!this.form.contact) {
        this.errForm.contact = "How would I reach you?"
        return false;
      }
      if(!this.form.message) {
        this.errForm.message = "Please leave me something"
        return false;
      }
      return true;
    },
    sendMessage() {
      if(this.isFormValid()) {
        let body = `${ this.form.message }\n\nFrom: ${ this.form.name }`;
        window.open(`mailto:aceixsmartx@gmail.com?subject=New+message+from+your+site&body=${ body }`);
      }
    }
  }
};
</script>

